.episodes-wrapper {
    @include center-content;
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px 0px;
}
.episode-card {
    @include card;
    width: 500px;
    height: auto;
    padding-bottom: 15px;
}

.episode-img {
    width: 100%;

    img {
        width: 100%;
        border-radius: 10px 10px 0 0;
    }
}

.podcast-app-header-content {
	display: flex;
	align-items: center;
}

.episode-header{
	h4{
		color: black;
		font-size: 18px;
        padding: 20px;
		margin: 0px;
	}
}

.center-button {
    @include center-content;
    margin: auto;
    width: 100%;
}



@media screen and (max-width: 425px) {
    .episodes-wrapper {
        width: 100%;
        padding: 10px;
    }

    .episode-card {
        width: 100%;

        &:hover {
            transform: none;
        }

        h4 {
            font-size: 16px;
        }
    }

    .center-button {
        a {
            font-size: 14px;
        }
    }
}