@import '../CSS/Home/main_home.scss';
@import '../CSS/General/main_general.scss';
@import '../CSS/mixins_colors.scss';

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
    font-family: "Poppins", serif;
    overflow: scroll;
    background-color: white;
}

.skip-link {
    position: absolute;
    top: -40px;
    left: 6px;
    background: $black;
    color: white;
    padding: 8px;
    text-decoration: none;
    border-radius: 4px;
    z-index: 1000;
    
    &:focus {
        top: 6px;
    }
}

.error-boundary {
    @include center-content;
    min-height: 50vh;
    padding: 20px;
    
    .error-content {
        text-align: center;
        
        h2 {
            color: $orange;
            margin-bottom: 16px;
        }
        
        p {
            margin-bottom: 24px;
            max-width: 500px;
        }
        
        .error-retry-btn {
            @include btn($white, $orange, 12px 24px);
            border: none;
            border-radius: 25px;
            cursor: pointer;
            transition: 0.3s ease;
            
            &:hover {
                @include orange-glow;
            }
        }
    }
}

a{
    text-decoration: none;
}

.row{
    width: 100%;
}

.error404{
    height: 80vh;
    @include flex(center, center);


    .error-message{
        text-align: center;
        h1{
            font-size: 10rem;
        }
        p{
            font-size: 2rem;
        }
    }
}

