@import '../../mixins_colors.scss';

button {
    border-radius: 25px;
    width: 200px;
    font-weight: 500;
    transition: 0.3s ease;
    border: none;
}

.orange_button {
    @include btn($white, $orange, 10px 20px);
    transition: 0.3s ease;

    &:hover {
        @include orange-glow;
    }
}

.white_button {
    @include btn(black, $white, 5px);
    @include soft-shadow;
}